body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.title {
  text-align: center;
  font-size: 50px;
}

.small-one {
    border-radius: 50%;
    width: 350px;
    height: 350px;
    position: absolute;
    top: 60%;
    left: 50%;
    margin-left: 50px;
    margin-top: -250px;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    cursor: pointer;
}

.small-two {
    border-radius: 50%;
    width: 350px;
    height: 350px;
    position: absolute;
    top: 60%;
    left: 50%;
    margin-left: -450px;
    margin-top: -250px;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    cursor: pointer;
}

.big {
    width: 100%;
    height: 100%;
    top: 0;
    margin-top: 0;
    left: 0;
    margin-left: 0;
    border-radius: 0;
}

.item {
  position: relative;
  text-align: center;
  top: 25%;
  font-size: 45px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}


